@import url("https://use.typekit.net/fxw8vnq.css");


h1 {
    font-size: 2.625rem;
    font-weight: normal;
    max-width: 591px;
    margin: 0 0 3rem;
}

h3 {
    font-size: 1.25rem;
    padding-left: 1.56rem;
}

h4 {
    font-size: 1.25rem;
    font-weight: normal;
}

p {
    font-size: 1rem;
}

.hi {
    font-size: 7.625rem;
    color: $accent;
    margin-bottom: 0;
}

.sub-heading {
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: center;
    max-width: 1250px;
    padding: 8rem 0 3rem;
    margin: auto;
}

@media #{$mq-760-down} {
    h1 {
        font-size: 2rem;
    }

    h3 {
        font-size: 1rem;
    }

    h4 {
        font-size: 1rem;
    }

    .hi {
        font-size: 6rem;
        padding-top: 2rem;
    }

    .sub-heading {
        padding: 4rem 2% 1rem;
    }
}