html {
    box-sizing: border-box;
    font-size: 100%;
    overflow-x: hidden;
    width: 100%;
}

*,
*:before,
*:after {
    box-sizing: inherit;
    transition: all .3s ease-in;
}

body {
    background-color: $white;
    color: $black;
    font-family: 'century-gothic', sans-serif;
    margin: 0;
    padding: 0;
}

ul li {
    list-style-type: none;
}

a {
    color: $black;
    text-decoration: none;
    transition: all .3s;
}

a:hover {
    color: $accent;
}



// UI elements, buttons
.line {
    background-color: $accent;
    width: 75px;
    height: 2px;
    opacity: .7;
}

.container {
    max-width: 1140px;
    padding: 0 2%;
    margin: 2rem auto;
}

.space {
    height: 250px;
    width: 100%;
}

@media #{$mq-760-down} {
    .container {
        padding: 0 5%;
        margin: 2rem auto 0;
    }

    .space {
        height: 120px;
    }
}

.parent {
    position: relative;
}