.top-filler {
  position: absolute;
  right: -50px;
  top: -52px;
  z-index: -99;
}

@media #{$mq-760-down} {
  .top-filler {
    display: none;
  }
}

// Nav
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;

  ul li {
    display: inline;

    a {
      margin-left: 100px;
    }
  }
}

.burger {
  display: none;
}

.burger span {
  width: 30px;
  height: 4px;
  background-color: $accent;
  margin: 3px;
  border-radius: 18px;
  transition: all 0.3s ease;
}



@media #{$mq-760-down} {
  .navigation ul li a {
    margin-left: 0;
  }

  .burger {
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    z-index: 100;
  }

  .nav-links {
    position: absolute;
    background-color: $white;
    right: 0;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    transition: transform 0.4s ease;
    transform: translateY(100%);
    text-align: center;
    opacity: 0;
    z-index: 99;

    li {
      font-size: 1.5rem;
      margin-top: 3rem;
    }
  }

  .nav-active {
    transform: translateY(0%);
    opacity: 1;
  }
}