.work-item {
  display: flex;
  justify-content: center;
  align-items: center;

  .side-text {
    max-width: 420px;
    margin: 2rem;
  }

  img {
    max-width: 520px;
    height: auto;
    border-radius: 6px;
  }

  i {
    margin-left: .7rem;
  }
}

.work-item:hover img {
  box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.2);
  transform: translateY(-4px);
}

.side-text:hover i {
  margin-left: 1rem;
}


.mobile-show {
  display: none;
}

.reverse {
  flex-direction: row-reverse;
}

@media #{$mq-900-down} {
  .work-item img {
    max-width: 420px;
  }
}

@media#{$mq-760-down} {
  .work-item {
    flex-direction: column;
    justify-content: center;

    .side-text {
      margin: 0;
      display: block;
    }

    img {
      width: 100%;
    }
  }
}

// Section  buttons 
.viewbutton {
  opacity: .7;
}

@media #{$mq-760-down} {
  .viewbutton {
    display: none;
  }

  .mobile-show {
    display: inline;
    opacity: .7;
  }
}